*{
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  outline: none;
}
@font-face {
  font-family: vazir;
  src: url(../public/fonts/vazir.woff);
}
body {
  direction: rtl;
  font-family: 'vazir' !important;
  background-color: rgb(73, 224 , 149) !important;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.container{
  background-color: #f8f8f8;
  margin: auto;
  width: 700px;
  padding: 30px;
}

h3{
  font-size: 25px;
  padding: 20px 0;
}


.question{
  font-family: 'vazir' !important;
  margin-top: 20px;
  padding: 20px;
  border: 10px;
  box-shadow: 0 0 10px rgba(117,117,177,0.3);
}

.question header{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.question p {
  font-size: 15px;
  color: #333;
  margin-top: 15px;
}


.question button{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: rgb(68,68,68);
  border: none;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
